.objectifs {
    background-color: #d9d9d9;
    color: black;
    border-radius: 10px;
    border: black 1px solid;
    font-size: 40px;
    margin: 10px;
    position: relative;
}

ul {
    display: inline-block;
    width: 100%
}

.fa-circle-plus {
    font-size: 2rem;
    color: #3d3939;
    background-color: #d9d9d9;
    transition: all 0.3s ease-out;
    border-radius: 110%;
    position: absolute;
    bottom: 0.5%;
    right: 0.5%;
}

.fa-circle-plus:hover {
    color: #d9d9d9;
    background-color: #3d3939;
    cursor: pointer;
}

p {
    margin: 0;
    padding-right: 40px;
}

.fa-circle-xmark {
    font-size: 2rem;
    color: #3d3939;
    background-color: #d9d9d9;
    transition: all 0.3s ease-out;
    border-radius: 110%;
    position: absolute;
    bottom: 0.5%;
    right: 0.5%;
}

.fa-circle-xmark:hover {
    color: #d9d9d9;
    background-color: #3d3939;
    cursor: pointer;
}


.username,
.email {
    padding-right: 0;
}

.searchedObjectifsButton {
    background-color: #980012;
    transition: all 0.3s ease-out;
    color: #d9d9d9;
    width: 25%;
    margin-left: 10px;
    padding: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
}

.searchedObjectifsButton:hover {
    background-color: #d9d9d9;
    color: #980012;
    transition: all 0.3s ease-out;
    cursor: pointer;
    border-radius: 10px;
}

.searchedObjectifs {
    width: 25%;
    height: 100%;
    float: right;
    margin: 0;
    padding: 0;
    border: none;
}

.searchedObjectifs:focus {
    border: none
}

.search-bar {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 3%;
    text-align: center;
    align-items: center;
}

#img_oeil {
    size: 1px
}